@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700|Open+Sans:400,700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');


body {
	font-family: "Titillium Web", sans-serif;
	font-weight: normal;
	font-style: normal;
	color: #444;
}

* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
	text-decoration: none;
	outline: none;
}

a:focus,
a:hover {
	text-decoration: none;
}

i,
span,
a {
	display: inline-block;
}

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Titillium Web", sans-serif;
	font-weight: 700;
	color: #222;
	margin: 0px;
}

h1 {
	font-size: 48px;
}

h2 {
	font-size: 36px;
}

h3 {
	font-size: 28px;
}

h4 {
	font-size: 22px;
}

h5 {
	font-size: 18px;
}

h6 {
	font-size: 16px;
}

ul,
ol {
	margin: 0px;
	padding: 0px;
	list-style-type: none;
}

p {
	font-size: 16px;
	font-weight: 400;
	line-height: 24px;
	color: #444;
	margin: 0px;
}

.bg_cover {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	width: 100%;
	height: 100%;
}

/*===== All Button Style =====*/
.main-btn {
	display: inline-block;
	font-weight: 500;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	border: 1px solid #011922;
	padding: 0 40px;
	font-size: 15px;
	line-height: 48px;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	z-index: 5;
	-webkit-transition: all 0.4s linear;
	-moz-transition: all 0.4s linear;
	-ms-transition: all 0.4s linear;
	-o-transition: all 0.4s linear;
	transition: all 0.4s linear;
	background-color: #011922;
	text-transform: uppercase;
}

.main-btn:hover {
	-webkit-box-shadow: 0px 10px 25px 0px rgba(61, 190, 238, 0.25);
	-moz-box-shadow: 0px 10px 25px 0px rgba(61, 190, 238, 0.25);
	box-shadow: 0px 10px 25px 0px rgba(61, 190, 238, 0.25);
	color: #fff;
}

.main-btn.main-btn-2 {
	background-color: #a1daef;
	color: #011922;
	border-color: #e8f9ff;
}

.main-btn.main-btn-2:hover {
	background-color: #011922;
	color: #fff;
	border-color: #011922;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
}

/*===== All Slick Slide Outline Style =====*/
.slick-slide {
	outline: 0;
}

/*===== All Section Title Style =====*/
.section_title .sub_title {
	font-size: 18px;
	font-weight: 600;
	color: #011922;
	text-transform: uppercase;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.section_title .sub_title {
		font-size: 16px;
	}
}

@media (max-width: 767px) {
	.section_title .sub_title {
		font-size: 16px;
	}
}

.section_title .main_title {
	font-size: 45px;
	font-weight: 600;
	color: #222;
	margin-top: 23px;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.section_title .main_title {
		font-size: 35px;
	}
}

@media (max-width: 767px) {
	.section_title .main_title {
		font-size: 28px;
	}
}

/*===== All Pagination Style =====*/
.pagination {
	padding-top: 50px;
}

@media (max-width: 767px) {
	.pagination {
		padding-top: 30px;
	}
}

.pagination .page-item {
	margin-right: 30px;
}

@media (max-width: 767px) {
	.pagination .page-item {
		margin-right: 15px;
	}
}

.pagination .page-item:last-child {
	margin-right: 0;
}

.pagination .page-item a {
	width: 45px;
	height: 45px;
	line-height: 43px;
	border: 1px solid #dadada;
	text-align: center;
	font-size: 18px;
	font-weight: 600px;
	color: #3dbeee;
	border-radius: 50%;
	-webkit-transition: all 0.3s linear;
	-moz-transition: all 0.3s linear;
	-ms-transition: all 0.3s linear;
	-o-transition: all 0.3s linear;
	transition: all 0.3s linear;
}

@media (max-width: 767px) {
	.pagination .page-item a {
		width: 35px;
		height: 35px;
		line-height: 35px;
		font-size: 14px;
	}
}

.pagination .page-item a.active,
.pagination .page-item a:hover {
	background-color: #3dbeee;
	border-color: #3dbeee;
	color: #fff;
}

/*===== All Overlay Style =====*/
.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(34, 34, 34, 0.7);
	z-index: 99;
	opacity: 0;
	display: none;
}

.overlay.active {
	display: block;
	opacity: 1;
}

/*===== All Form Focus Style =====*/
.form-control {
	color: #444;
}

.form-control::placeholder {
	color: #444;
}

.form-control::-moz-placeholder {
	color: #444;
}

.form-control::-moz-placeholder {
	color: #444;
}

.form-control::-webkit-input-placeholder {
	color: #444;
}

.form-control:focus {
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	border-color: #dadada;
	color: #444;
}

/*===== All Preloader Style =====*/
.preloader {
	/* Body Overlay */
	position: fixed;
	top: 0;
	left: 0;
	display: table;
	height: 100%;
	width: 100%;
	/* Change Background Color */
	background: #fff;
	z-index: 99999;
}

.preloader .loader {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
}

.preloader .loader .ytp-spinner {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 64px;
	margin-left: -32px;
	z-index: 18;
	pointer-events: none;
}

.preloader .loader .ytp-spinner .ytp-spinner-container {
	pointer-events: none;
	position: absolute;
	width: 100%;
	padding-bottom: 100%;
	top: 50%;
	left: 50%;
	margin-top: -50%;
	margin-left: -50%;
	-webkit-animation: ytp-spinner-linspin 1568.23529647ms linear infinite;
	-moz-animation: ytp-spinner-linspin 1568.23529647ms linear infinite;
	-o-animation: ytp-spinner-linspin 1568.23529647ms linear infinite;
	animation: ytp-spinner-linspin 1568.23529647ms linear infinite;
}

.preloader .loader .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator {
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-animation: ytp-spinner-easespin 5332ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-moz-animation: ytp-spinner-easespin 5332ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-o-animation: ytp-spinner-easespin 5332ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	animation: ytp-spinner-easespin 5332ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
}

.preloader .loader .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator .ytp-spinner-left {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	overflow: hidden;
	right: 50%;
}

.preloader .loader .ytp-spinner .ytp-spinner-container .ytp-spinner-rotator .ytp-spinner-right {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	left: 50%;
}

.preloader .loader .ytp-spinner-circle {
	box-sizing: border-box;
	position: absolute;
	width: 200%;
	height: 100%;
	border-style: solid;
	/* Spinner Color */
	border-color: #3dbeee #3dbeee #dadada;
	border-radius: 50%;
	border-width: 6px;
}

.preloader .loader .ytp-spinner-left .ytp-spinner-circle {
	left: 0;
	right: -100%;
	border-right-color: #dadada;
	-webkit-animation: ytp-spinner-left-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-moz-animation: ytp-spinner-left-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-o-animation: ytp-spinner-left-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	animation: ytp-spinner-left-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
}

.preloader .loader .ytp-spinner-right .ytp-spinner-circle {
	left: -100%;
	right: 0;
	border-left-color: #dadada;
	-webkit-animation: ytp-right-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-moz-animation: ytp-right-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	-o-animation: ytp-right-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
	animation: ytp-right-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both;
}

/* Preloader Animations */
@-webkit-keyframes ytp-spinner-linspin {
	to {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@keyframes ytp-spinner-linspin {
	to {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

